import config from '../config/config.js'

const PROD_URL = config.build.url //  api url

const isCrossDomain = config.isCrossDomain
const consoleConfig = config.console

export const getApiUrl = api => {
    if (isCrossDomain && process.env.NODE_ENV === 'production') {
        return `${PROD_URL}/${api}`; //
    } else {
        return `/api/${api}`;
    }
}

export const logger = {
    info: consoleConfig.show && consoleConfig.level === 'info' ? console.log : () => {}, // @todo  --  GetPost
    debug: consoleConfig.show && consoleConfig.level === 'debug' ? console.log : () => {}, // @todo  -- 
    warn: consoleConfig.show && consoleConfig.level === 'warn' ? console.log : () => {}, // @todo  --  bug 
    error: consoleConfig.show && consoleConfig.level === 'error' ? console.log : () => {} //@todo  --  catch 
}